/*
font-family: 'Goudy Bookletter 1911', serif;
font-family: 'Roboto', sans-serif;
*/

html, body {
  font-family: 'Roboto', sans-serif;
  background-color: black;
  font-size: 10px;
}

.spacer {
  min-width: 100%;
  min-height:10vh;
}

#navbar {
  position: fixed;
  width: 100%;
  height: 10vh;
  top: 0;
  left: 0;
  background-color: black;
  opacity: .9;
  font-size: 2rem;
  font-family: 'Goudy Bookletter 1911', serif;
}

#navbar ul {
  list-style: none;
  display: flex;
  flex-direction: row-reverse;
}

#navbar li {
  width: 100px;
  height: 100%;
}
@media (max-width: 815px) {
  #navbar {
    height: auto;
    margin: 0;
  }

  #navbar ul {
    flex-direction: column-reverse;
    height: 100%;
    text-align: center;
    margin: 0;
    padding: 2% 0;
  }

  #navbar li {
    width: 100%;
    display: block;
  }
}


#navbar a {
  text-decoration: none;
  color: white;
}

#navbar a:hover {
  color: #EFEFEF;
}

#welcome-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  color: white;
  font-size: 1.5rem;
}

#welcome-section h1 {
  font-family: 'Goudy Bookletter 1911', serif;
  font-size: 3rem;
}

#projects {
  width: 100vw;
  max-width: 100vw;
  height: auto;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: white;
  font-size: 1.5rem;
  background-color: black;
}

@media (max-width: 815px) {
  #projects {
    overflow-y: scroll;
  }
}

#projects {
  padding-bottom: 10vh;
}

#projects h2 {
  font-family: 'Goudy Bookletter 1911', serif;
  font-size: 2.5rem;
  padding-bottom: 10vh;
  margin: 0;
}

.projects-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  grid-gap: 5%;
  width: 80%;
  max-width: 80%;
  margin: auto;
  padding: 0 10% 10vh 10%;
}


body::after {
  content:'';
  display: block;
  height: 50px;
}
